body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #ffffff;
  background-image: url("board.jpg");
  min-height: 100vh;
  background-size: 100% auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
}

.App-link {
  color: #61dafb;
}

input[type=number] {
  font-size: calc(10px + 2vmin);
  width: 4em;
}

button {
  background-color: #000000; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
}


textarea {
  font-size: calc(10px + 2vmin);
}

.ContractInfo {
  font-size: calc(10px + 1vmin);
  text-align: left;
  width: 90vw;
  height: 7em;
  padding: 2vw;
  overflow-x: scroll;
  white-space: pre;
}

.square {
  width:50px;
  height:50px;
  padding:0px;
  background-color: #ffffff;
  border: 2px solid #000000;
  color: #000000;
  line-height: 0px;
}



.chessboard-row:first-child .chessboard-cell{
  border: none;
}
.chessboard-col:first-child .chessboard-cell{
  border: none;
}
.chessboard-row{
  position: relative;
}
.chessboard-cell{
  border: 1px solid rgb(131, 130, 130);
  width: 25px;
  height: 25px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  box-sizing: border-box;
}
.chessboard-cell-click{
  width: 20px;
  height: 20px;
  position: absolute;
  z-index: 12;
  margin-right: -10px;
  margin-bottom: -10px;
}
.chessboard-cell-black{
  z-index: 99;
  margin-right: -10px;
  margin-bottom: -10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #333333;
  box-shadow: 0px 2px 5px 2px #eee;
}
.chessboard-cell-white{
  z-index: 99;
  margin-right: -10px;
  margin-bottom: -10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0px 2px 5px 2px rgb(226, 226, 226);
  
}